                            <td :key="j+'-'+k+'-td-input-table-percent'" :class="'nowrap-table'" v-if="j===0 || a[b.data] !== c.data[j-1][b.data] || a[c.config[0].data] !== c.data[j-1][c.config[0].data]" :rowspan="c.data.filter(x=> (x[c.config[0].data] === a[c.config[0].data])).length"> {{ a.total_prosentase || 0 }} % </td>
                          </template>
                        </template>
                      </template>
                      <template v-else>
                        <template v-for="(c,l) in a[b.data]">
                          <td :key="l+'-'+j+'-'+k+'-td-input-table'" class="nowrap-table" :class="globalFilter.agenda_id === -Math.abs(l+1)? 'bg-light-primary': ''">
                            <div class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ c.value | parse(b.pipe) }}</span>
                            </div>
                          </td>
                          <template v-if="b.percent">
                            <td :key="l+'-'+j+'-'+k+'-td-percent-table'" class="nowrap-table text-right" :class="globalFilter.agenda_id === -Math.abs(l+1)? 'bg-light-primary': ''">
                              <span>{{ c.prosentase | parse('number') }}%</span>
                            </td>
                          </template>
                        </template>
                      </template>
                    </template>
                  </template>
                </tr>
              </tbody>
            </table>
          </div>
          <hr style="border-style: dashed">
          <h4>Biaya Usaha Setelah Pembebanan </h4>
          <div v-if="mainData" class="table-responsive mb-0">
            <table v-if="mainData.data.length" class="table table-head-custom table-bordered table-vertical-cente table-input">
              <thead>
                <tr class="bg-light-info">
                  <template v-for="(th,i) in tableConfig">
                    <th rowspan="2" v-if="!th.multiple" :class="th.class" class=" text-center" :key="i+'-header-table'" v-html="th.label" style="color: #000 !important;vertical-align:middle !important;"></th>
                    <template v-else>
                      <template v-if="th.multiple === 'total'">
                        <th rowspan="2" colspan="2" :key="i+'-thead-total'" class=" text-center" style="color: #000 !important;vertical-align:middle !important;">Total</th>
                      </template>
                      <template v-else>
                        <th v-for="(thMultiple, j) in mainData.data[0][th.data]" :key="j+'-'+i+'-thead-config'" :class="globalFilter.agenda_id === -Math.abs(j+1)? 'bg-primary-primary': ''" class="nowrap-table min-w-150px text-center" style="color: #000 !important;vertical-align:middle !important;">
                          <span>{{ thMultiple[th.label] | parse(th.multiple) }}</span>
                          <span v-if="globalFilter.agenda_id!==0" class="font-size-xs d-block text-info">{{ Math.abs(globalFilter.agenda_id) >= j+1 ? 'Actual': 'Forecast' }}</span>
                        </th>
                      </template>
                    </template>
                  </template>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(a, j) in mainData.data" :key="j+'-tr-body'">
                  <template v-for="(b, k) in tableConfig">
                    <template v-if="!b.multiple">
                      <template v-if="!b.wrap">
                        <template v-if="b.data === 'total'">
                          <td :key="j+'-'+k+'-td-input-table'" :class="b.class || 'nowrap-table'">
                            <div v-if="b.money" class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ a['total_tambahan_bud'] || 0 | parse(b.pipe) }}</span>
                            </div>
                            <div v-else-if="b.action" class="d-flex align-items-center justify-content-between">
                              <div class="d-flex align-items-center justify-content-between">
                                <span>{{ a['total_tambahan_bud'] | parse(b.pipe) }}</span>
                              </div>
                              <i v-if="a.checker" v-b-tooltip.right :title="a.checker_description || ''" class="ri-check-line pl-5 text-success"></i>
                              <i v-else v-b-tooltip.right :title="a.checker_description || ''" class="ri-close-line pl-5 text-danger"></i>
                            </div>
                            <template v-else> {{ a['total_tambahan_bud'] | parse(b.pipe) }} </template>
                          </td>
                        </template>
                        <template v-else>
                          <td :key="j+'-'+k+'-td-input-table'" :class="b.class || 'nowrap-table'">
                            <div v-if="b.money" class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ a[b.data] || 0 | parse(b.pipe) }}</span>
                            </div>
                            <div v-else-if="b.action" class="d-flex align-items-center justify-content-between">
                              <div class="d-flex align-items-center justify-content-between">
                                <span>{{ a[b.data] | parse(b.pipe) }}</span>
                              </div>
                              <i v-if="a.checker" v-b-tooltip.right :title="a.checker_description || ''" class="ri-check-line pl-5 text-success"></i>
                              <i v-else v-b-tooltip.right :title="a.checker_description || ''" class="ri-close-line pl-5 text-danger"></i>
                            </div>
                            <template v-else> {{ a[b.data] | parse(b.pipe) }} </template>
                          </td>
                        </template>
                      </template>
                      <template v-else>
                        <td :key="j+'-'+k+'-td-input-table'" :class="b.class || 'nowrap-table'" v-if="j===0 || a[b.data] !== c.data[j-1][b.data] || a[c.config[0].data] !== c.data[j-1][c.config[0].data]" :rowspan="c.data.filter(x=> (x[c.config[0].data] === a[c.config[0].data])).length">
                          <div v-if="b.money" class="d-flex align-items-center justify-content-between px-3">
                            <span class="pr-3">Rp.</span>
                            <span>{{ a[b.data] || 0 | parse(b.pipe) }}</span>
                          </div>
                          <div v-else-if="b.action" class="d-flex align-items-center justify-content-between">
                            <div class="d-flex align-items-center justify-content-between">
                              <span>{{ a[b.data] | parse(b.pipe) }}</span>
                            </div>
                            <i v-if="a.checker" class="ri-check-line pl-5 text-success"></i>
                            <i v-else class="ri-close-line pl-5 text-danger"></i>
                          </div>
                          <template v-else> {{ a[b.data] | parse(b.pipe) }} </template>
                        </td>
                      </template>
                    </template>
                    <template v-else>
                      <template v-if="b.multiple === 'total'">
                        <template v-if="!b.wrap">
                          <td class="nowrap-table" :key="k+'-thead-total'">
                            <div class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ a[b.data] || 0 | parse('number') }}</span>
                            </div>
                          </td>
                          <td class="nowrap-table text-right" :key="k+'-tbody-total-percent'">{{ a.total_prosentase || 0 }} %</td>
                        </template>
                        <template v-else>
                          <td :key="j+'-'+k+'-td-input-table'" :class="b.class || 'nowrap-table'" v-if="j===0 || a[b.data] !== c.data[j-1][b.data] || a[c.config[0].data] !== c.data[j-1][c.config[0].data]" :rowspan="c.data.filter(x=> (x[c.config[0].data] === a[c.config[0].data])).length">
                            <div class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ a[b.data] || 0 | parse('number') }}</span>
                            </div>
                          </td>
                          <template>
                            <td :key="j+'-'+k+'-td-input-table-percent'" :class="'nowrap-table'" v-if="j===0 || a[b.data] !== c.data[j-1][b.data] || a[c.config[0].data] !== c.data[j-1][c.config[0].data]" :rowspan="c.data.filter(x=> (x[c.config[0].data] === a[c.config[0].data])).length"> {{ a.total_prosentase || 0 }} % </td>
                          </template>
                        </template>
                      </template>
                      <template v-else>
                        <template v-for="(c,l) in a[b.data]">
                          <td :key="l+'-'+j+'-'+k+'-td-input-table'" class="nowrap-table" :class="globalFilter.agenda_id === -Math.abs(l+1)? 'bg-light-primary': ''">
                            <div class="d-flex align-items-center justify-content-between px-3">
                              <span class="pr-3">Rp.</span>
                              <span>{{ c.value_tambahan_bud | parse(b.pipe) }}</span>
                            </div>
                          </td>
                        </template>
                      </template>
                    </template>
                  </template>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-auction-plans',
  data () {
    return {
      isLoading: false,
      fullscreen: false,
      config: {
        title: 'Input Pemasaran',
        permission: {
          create: 'create-auction-plans',
          read: 'view-auction-plans',
          update: 'do-verification-request-auction-plans',
          delete: 'delete-auction-plans'
        }
      },
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('YYYY') : ''
        },
        parse: (value) => {
          return value ? moment(value, 'YYYY').toDate() : null
        }
      },
      baseAPI: 'auction-plans',
      searchFilter: '',
      globalFilter: {},
      mainData: null,
      mainConfigData: null,
      mainForm: {
        division_id: { label: null, code: null },
        periode_year: moment().set('date', 1).set('month', 0).format('YYYY'),
        agenda_id: { label: null, code: null },
        scenario_input: { label: null, code: null }
      },
      mainConfigForm: {
        periode_year: moment().set('date', 1).set('month', 0).format('YYYY'),
        scenario_input: { code: 'moderate', label: 'Skenario Moderat' },
        agenda_id: { label: null, code: null }
      },
      moduleList: [
        // { code: 'custom/data-input-is-biaya-keuangan', id: 'BK', export: 'export-excel/template-is-biaya-keuangan', import: 'custom/data-input-is-biaya-keuangan/import-template', label: 'Biaya Keuangan', queue: 'import-data-input-is-biaya-keuangan' },
        // { code: 'custom/data-input-is-bukp', id: 'BUKP', export: 'export-excel/template-is-bukp', import: 'custom/data-input-is-bukp/import-template', label: 'Beban Usaha KP', queue: 'import-data-input-is-bukp' },
        { code: 'CONFIG', id: 'CONFIG', api: 'custom/config-asumsi-beban-usaha-divisions', summary: 'custom/asumsi-beban-usaha-divisions', label: 'Konfigurasi Beban' }
      ],
      activeModule: 'CONFIG',
      datasetList: {
        division: {
          api: 'divisions',
          param: { sort: 'ASC', sort_by: 'division_id' },
          option: { label: 'division_name', code: 'id' },
          data: [],
          target: 'division_id'
        },
        agenda: { api: null, param: null, option: null, data: [], target: null },
        scenario: {
          api: null,
          param: null,
          option: null,
          data: [
            { code: 'pesimist', label: 'Skenario Pesimis' },
            { code: 'moderate', label: 'Skenario Moderat' },
            { code: 'optimist', label: 'Skenario Optimis' }
          ],
          target: null
        }
      },
      isUploading: false,
      uploadMax: 1,
      uploadLimitExceed: false,
      uploadError: null,
      uploadPercentage: 0,
      tableConfig: [],
      queueKey: 0
    }
  },
  watch: {
    mainForm: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e && e.division_id.code) { // hehe power of await
          this.globalFilter = Object.assign({}, this.globalFilter, {
            agenda_id: e.agenda_id.code,
            division_id: e.division_id.code,
            periode_year: moment(moment(e.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD'),
            scenario_input: e.scenario_input.code
          })
        }
      }
    },
    mainConfigForm: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e && e.agenda_id.label && this.activeModule === 'CONFIG') this.getConfigData()
        else this.$set(this.mainForm, 'scenario_input', this.datasetList.scenario.data[1])
      }
    },
    // globalFilter: {
    //   deep: true,
    //   immediate: false,
    //   handler (e) {
    //     if (e) {
    //       this.getMainData(e)
    //     }
    //   }
    // },
    activeModule: {
      deep: false,
      immediate: false,
      handler (e) {
        if (e) {
          if (e === 'CONFIG') this.getConfigData()
          else this.queueKey++
          // else this.getMainData(this.globalFilter)
        }
      }
    }
  },
  mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
    this.getDataset()
    // console.log(navigator.connection)
  },
  methods: {
    staticDataset () {
      let result = []
      for (let i = 0; i < 13; i++) {
        const e = -Math.abs(i)
        result.push({ label: i === 0 ? 'RKAP' : 'Stress Test ' + moment().set('date', 1).set('month', i - 1).format('MMMM'), code: i === 0 ? i : e })
      }
      this.$set(this.datasetList.agenda, 'data', result)
      this.$set(this.mainForm, 'agenda_id', result[0])
      this.$set(this.mainConfigForm, 'agenda_id', result[0])
    },
    buidConfigTable (e) {
      let mainConfig = [
        // { data: 'rel_division_id', label: 'Divisi', money: false, pipe: '', class: 'nowrap-table text-left' },
        // { data: 'rel_component_id', label: 'Kode Komponen', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
        // { data: 'rel_akun_id', label: 'Kode Akun', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
        // { data: 'item_name', label: 'Uraian', money: false, class: 'nowrap-table sl', action: false },
        // { data: 'total', label: 'Total', money: true, class: 'nowrap-table text-center', pipe: 'number' }
      ]
      let mainTableConfig = [
        { data: 'division_code', label: 'Kode Divisi', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
        { data: 'division_name', label: 'Nama Divisi', money: false, pipe: 'uppercase', class: 'nowrap-table' },
        { data: 'pjt_npjt', label: 'PJT /<br>Non PJT', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
        { data: 'total', label: 'Total', money: true, class: 'nowrap-table text-center', pipe: 'number' }
      ]

      let addConfig = []
      if (e === 'custom/data-input-is-bukp') {
        addConfig = [
          { data: 'rel_division_id', label: 'Divisi', money: false, pipe: '', class: 'nowrap-table text-left' },
          { data: 'rel_component_id', label: 'Kode Komponen', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'rel_akun_id', label: 'Kode Akun', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'item_name', label: 'Uraian', money: false, class: 'nowrap-table sl', action: false },
          { data: 'total', label: 'Total', money: true, class: 'nowrap-table text-center', pipe: 'number' }
        ]
      } else if (e === 'custom/data-input-is-biaya-keuangan') {
        addConfig = [
          { data: 'project_name', label: 'Nama Proyek', money: false, class: 'nowrap-table sl', action: true },
          { data: 'rel_interest_calcs_id', label: 'Jenis Pembiayaan', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'operation_type', label: 'JO/NJO', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'source_of_income', label: 'INT/EKS', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'jtts_type', label: 'JTTS/NON JTTS', money: false, pipe: 'uppercase', class: 'nowrap-table text-center' },
          { data: 'total', label: 'Total', money: true, class: 'nowrap-table text-center', pipe: 'number' }
        ]
      }
      if (e !== 'CONFIG') {
        addConfig.push({
          data: 'data_series_months',
          label: 'periode_month',
          multiple: 'longMonth',
          money: true,
          pipe: 'number'
        })
        this.tableConfig = [...mainConfig, ...addConfig]
      } else {
        addConfig.push({
          data: 'data_series_months',
          label: 'periode_month',
          multiple: 'longMonth',
          money: true,
          pipe: 'number',
          percent: true
        })
        this.tableConfig = [...mainTableConfig, ...addConfig]
      }
    },
    async getDataset () {
      await this.staticDataset()
      for (let i = 0; i < Object.keys(this.datasetList).length; i++) {
        const e = Object.keys(this.datasetList)[i]
        if (this.datasetList[e].api) {
          this.$_api.dataset(this.datasetList[e].api, this.datasetList[e].param).then(res => {
            let result = res.data.map(x => { return { label: x[this.datasetList[e].option.label], code: x[this.datasetList[e].option.code], data: x } })
            this.$set(this.datasetList[e], 'data', result)
            if (this.datasetList[e].target && result.length) this.$set(this.mainForm, this.datasetList[e].target, result[0])
          }, err => {
            this.$_alert.error(err)
          })
        }
      }
    },
    downloadImport () {
      this.isLoading = true
      const selected = this.moduleList.filter(x => x.code === this.activeModule)[0]
      let xlsTitle = this.mainForm.division_id.data.division_code
      xlsTitle += '_' + selected.id
      xlsTitle += this.globalFilter.agenda_id === 0 ? '_RKAP' : '_ST-' + moment().set('date', 1).set('month', (Math.abs(this.globalFilter.agenda_id) - 1)).format('MMM') + '_' + this.mainForm.scenario_input.code
      xlsTitle += '_' + moment(this.globalFilter.periode_year).format('YYYY')
      this.$_api.downloadFile(selected.export, this.globalFilter, xlsTitle + '.xlsx').then(res => {
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    uploadImport (e) {
      this.uploadPercentage = 0
      this.uploadError = null
      const api = this.moduleList.filter(x => x.code === this.activeModule)[0].import
      if ((e.target.files[0].size / 1000000).toFixed(2) < this.uploadMax) {
        this.isUploading = true
        this.uploadLimitExceed = false
        let file = e.target.files[0]
        let formData = new FormData()
        let form = JSON.parse(JSON.stringify(this.globalFilter))
        form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
        for (let i = 0; i < Object.keys(form).length; i++) {
          const e = Object.keys(form)[i]
          formData.append(e, form[e])
        }
        formData.append('file', file)
        this.$_api.uploadFile(api, formData, event => {
          this.uploadPercentage = Math.round((100 * event.loaded) / event.total)
        }).then(res => {
          this.isUploading = false
          e.target.value = null
          this.queueKey++
          this.getMainData(this.globalFilter)
        }).catch((err) => {
          this.isUploading = false
          this.uploadError = err.data ? err.data.message : err.message
          e.target.value = null
        })
      } else this.uploadLimitExceed = true
    },
    formatDate (type) {
      let _format
      switch (type) {
        case 'date':
        case 'date-range':
          _format = 'YYYY-MM-DD'
          break
        case 'datetime':
        case 'datetime-range':
          _format = 'YYYY-MM-DD HH:mm:ss'
          break
        case 'year':
        case 'year-range':
          _format = 'YYYY'
          break
        case 'month':
        case 'month-range':
          _format = 'YYYY-MM-DD'
          break
        case 'time':
        case 'time-range':
          _format = 'HH:mm:ss'
          break
        case 'week':
        case 'week-range':
          _format = 'DD'
          break
      }
      return _format
    },
    async getMainData (e) {
      this.mainData = null
      this.isLoading = true
      let form = JSON.parse(JSON.stringify(e))
      form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
      if (this.activeModule === 'custom/data-input-is-bukp') delete form.division_id
      const api = this.moduleList.filter(x => x.code === this.activeModule)[0].code
      await this.buidConfigTable(api)
      this.$_api.dataset(api, form).then(res => {
        this.mainData = res
        this.isLoading = false
      }, err => {
        this.mainData = null
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    async getConfigData () {
      this.isLoading = true
      const selected = this.moduleList.filter(x => x.code === this.activeModule)[0]
      let form = {
        periode_year: moment(moment(this.mainConfigForm.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD')
      }
      const api = selected.api
      this.$_api.dataset(api, form).then(res => {
        this.mainConfigData = res
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
      this.getConfigSummary()
    },
    async getConfigSummary (e = null) {
      this.isLoading = true
      const selected = this.moduleList.filter(x => x.code === this.activeModule)[0]
      let form = {
        periode_year: moment(moment(this.mainConfigForm.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD'),
        scenario_input: this.mainConfigForm.scenario_input.code,
        agenda_id: this.mainConfigForm.agenda_id.code
      }
      if (e) form.scenario_input = e.code
      await this.buidConfigTable('CONFIG')
      this.$_api.dataset(selected.summary, form).then(res => {
        this.mainData = res
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    calcRest (from, value, max, setter) {
      if (value < 100) {
        const result = max - value
        this.$set(this.mainConfigData['data_prosentase']['data'], setter, result)
      } else {
        this.$set(this.mainConfigData['data_prosentase']['data'], from, 100)
        this.$set(this.mainConfigData['data_prosentase']['data'], setter, 0)
      }
    },
    updateConfigProsen () {
      this.isLoading = true
      this.$_api.update('custom/config-asumsi-beban-usaha-prosentase', this.mainConfigData['data_prosentase']).then(async res => {
        this.isLoading = false
        this.getConfigSummary()
        this.$_alert.success('Prosentase Diperbarui')
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    updateConfigChecklist (e, value) {
      this.isLoading = true
      let form = Object.assign({}, e)
      form.pjt_npjt = value
      this.$_api.update('custom/config-asumsi-beban-usaha-divisions', form).then(async res => {
        this.isLoading = false
        this.getConfigSummary()
        this.$_alert.success('Kategori Diperbarui')
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    }
  }
}
</script>
<style scoped>
  .timeline.timeline-6:before {
    left: 1.5px !important;
  }
  .form-control.custom-dateyears {
    height: calc(1.5em + 1.19rem) !important;
  }
  .fullscreen-component {
    position: absolute !important;
    padding: 0 !important;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    z-index: 9999999999999999 !important;
    overflow-y: scroll;
  }
  .fullscreen-component .card {
    min-height: 100%;
  }
  .table-input .sr {
    position: sticky;
    right: 0; /* border */
  }
  .table-input .sl {
    position: sticky;
    left: -1px; /* border */
  }
  .table-input th.sl,
  .table-input th.sr {
    background: var(--info-light);
  }
  .table-input td.sl,
  .table-input td.sr {
    background: #fff;
  }
  .table-input td {
    padding: 3px 10px;
  }
  .download-div {
    min-width: 270px;
    max-width: 270px;
    border: 2px solid #ebedf3;
    border-radius: 8px;
  }
</style>



